export const getMixinUrl = (from = '/') => {
  const rootURl = 'https://www.mixin.one/oauth/authorize'

  const options = {
    client_id: '7ac9341f-61c1-4b49-b466-298397fe1624',
    redirect_uri: 'https://betxin.one/oauth/redirect',
    // redirect_uri: 'http://localhost:3000/oauth/redirect',
    scope: 'PROFILE:READ+SNAPSHOTS:READ',
    response_type: 'code',
    state: '/'
  }

  const qs = new URLSearchParams(options)

  return `${rootURl}?${qs.toString()}`
  // window.location.href = mixinUrl
}
