
import { IUserInfo } from '@/service/main/user/types'
import { useStore } from '@/store'
import { computed, ComputedRef, defineComponent, ref } from 'vue'
import { imageConfig } from '../config/image.config'
import { buttonConfig } from '../config/button.config'

export default defineComponent({
  props: {
    popConfig: {
      type: Object,
      required: true
    }
  },
  setup() {
    const store = useStore()
    const showPopup = ref(false)
    const handleShowPopup = () => {
      showPopup.value = true
    }

    const userInfo: ComputedRef<IUserInfo> = computed(
      () => store.state.login.userInfo
    )

    const buyCount = computed(() => store.state.main.userToTopicCount)
    const collectCount = computed(() => store.state.main.userCollectTopicCount)

    return {
      showPopup,
      userInfo,
      imageConfig,
      buttonConfig,
      buyCount,
      collectCount,
      handleShowPopup
    }
  }
})
