
import { useStore } from '@/store'
import { computed, defineComponent, ref } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { PieChart } from '@/components/page-echarts/index'
import NavOrder from '@/components/nav-order/index'
import { orderConfig } from '../config/order.config'
import { getPageData, postPageData } from '@/service/main/system/system'

export default defineComponent({
  components: {
    PieChart,
    NavOrder
  },
  setup() {
    const isBuied = ref(false)
    const btnText = ref('Buy')
    const NavOrderRef = ref<InstanceType<typeof NavOrder>>()
    const router = useRouter()
    const route = useRoute()
    const store = useStore()
    let topic = computed(() => store.state.main.singleTopic)

    const getTopicInfo = async () => {
      await getPageData(`/topic/${route.params.uuid}`).then((res) => {
        store.commit('main/changeSingleTopic', res.data)
      })
    }
    getTopicInfo()

    const handleBackClick = () => {
      router.push('/')
    }

    const checkTopicBuied = async () => {
      if (store.state.login.userInfo.mixin_uuid) {
        const res = await postPageData(`/usertotopic/check`, {
          user_id: store.state.login.userInfo.mixin_uuid,
          tid: route.params.uuid
        })
        if (res.data != null) {
          isBuied.value = true
          const res = await postPageData(`usertotopic/${route.params.uuid}`)
          if (parseFloat(res.data.yes_ratio) !== 0) {
            btnText.value = `You have paid YES $${res.data.yes_ratio_price}`
          } else {
            btnText.value = `You have paid NO ${res.data.no_ratio_price}`
          }
        }
      }
    }
    checkTopicBuied()

    const handleBtnClick = () => {
      if (!store.state.login.userInfo.mixin_uuid) {
        store.dispatch('login/accountLoginAction', route.path)
      }
      NavOrderRef.value?.handleShowPopup()
    }

    return {
      isBuied,
      topic,
      NavOrderRef,
      orderConfig,
      btnText,
      handleBackClick,
      handleBtnClick
    }
  }
})
