import { createStore, Store, useStore as useVueStore } from 'vuex'
import { IRootState, IStoreType } from './types'
import login from './login/login'
import main from './main/main'
import order from './order/order'
import { postPageData } from '@/service/main/system/system'

const store = createStore<IRootState>({
  state() {
    return {
      categoryCount: 0,
      categoryList: [],
      topicCount: 0,
      topicList: []
    }
  },
  getters: {},
  mutations: {
    changeCategoryList(state, list: any) {
      state.categoryList = list
    },
    changeCategoryCount(state, count: number) {
      state.categoryCount = count
    },
    changeTopicList(state, list: any) {
      state.topicList = list
    },
    changeTopicCount(state, count: number) {
      state.topicCount = count
    }
  },
  actions: {
    async getInitDataAction({ commit, dispatch }) {
      const categoryResult = await postPageData('/category/list', {
        offset: 0,
        limit: 100
      })
      const { totalCount: categoryCount, list: categoryList } =
        categoryResult.data
      commit('changeCategoryList', categoryList)
      commit('changeCategoryCount', categoryCount)

      dispatch('main/initMainAction')
      dispatch('order/initOrderAction')
    }
  },
  modules: {
    order,
    login,
    main
  }
})

export function useStore(): Store<IStoreType> {
  return useVueStore()
}

export function setupStore() {
  store.dispatch('getInitDataAction')
}

export default store
